@import url('https://fonts.googleapis.com/css?family=Raleway:900|Barlow:300,400,500');

body {
	font-family: 'Barlow', sans-serif;
	color: #1E1E1E;
	margin: 0;
	padding: 0;
}

h1 {
	font-family: 'Raleway', sans-serif;
	font-weight: 900;
	text-transform: uppercase;
}

@-webkit-keyframes fadeIn { 
  0% { opacity: 0; } 
  100% { opacity: 1; }
}

@keyframes fadeIn { 
  0% { opacity: 0; } 
  100% { opacity: 1; }
}

.App {
	-webkit-animation: fadeIn 0.5s ease;
	        animation: fadeIn 0.5s ease;
	height: 100vh;
}

.fixed {
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.main-div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex: 1 1;
	    flex: 1 1;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.MainLinks {
	font-family: 'Raleway', sans-serif;
	font-weight: 900;
	text-transform: uppercase;
	-webkit-transform: rotate(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotate(-90deg);
}

.about {
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.about .main {
		padding-bottom: 5px;
	}

.about .main .name {
			margin: 0;
			display: block;
			font-size: 4.5em;
			line-height: 0.85em
		}

.about .main .name.britti {
	font-size: calc(4.5em * 0.85);
}

.about .blurb {
		font-weight: 300;
		font-size: 22px;
	}

.about .blurb p {
			margin: 0;
			letter-spacing: 1px;
		}

.about .links {
	font-weight: 400;
	width: 100%;
	display: block;
	padding-top: 2px;
}

.about .links a {
		color: #2e2e2e;
		text-decoration: none;
		border-bottom: 1px #3e3e3e dashed;
		margin: 0 4px
	}

.about .links a:hover {
	color: #FC4C5D;
	border-bottom-color: #FC4C5D;
}

.about .links a:first-child {
	margin: 0 4px 0 0;
}

.about .links a:last-child {
	margin: 0 0 0 4px;
}

@media only screen and (max-width: 800px) and (orientation:portrait) {
	.fixed {
		-ms-flex-direction: column;
		    flex-direction: column;
	}
	.scroll-prompt {
		display: none;
	}
	.main-div.left {
		-ms-flex: 1.5 1;
		    flex: 1.5 1;
		margin-top: 40px;
	}
	.main-div.right {
		margin-bottom: 40px;
	}
	.about .main .name {
		font-size: 3.8em;
		line-height: 0.85em
	}
	.about .main .name.britti {
		font-size: calc(3.8em * 0.85);
	}
	.App .about .blurb {
		font-size: 20px;
	}
}

@media only screen and (max-width: 420px) {
	.App .about .blurb {
		font-size: 16px;
	}
}

@media only screen and (max-width: 830px) {
	.App .about .blurb {
		font-size: 16px;
	}
	.scroll-prompt {
		display: none;
	}
	.main-div.left {
		margin-left: 10px;
	}
	.main-div.right {
		margin-right: 10px;
	}
	.header {
		padding: 20px;
	}
}


