.ProjectEntry {
	margin: 0 40px;
	border-bottom: 3px #1e1e1e solid;
	position: relative;
	cursor: pointer;
}
	.ProjectEntry h1 {
		position: relative;
		margin: 32px 0;
	}
	.ProjectEntry .project-container {
		position: relative;
		display: inline-block;
	}
	.ProjectEntry .project-container .highlight {
			position: absolute;
			top: 27px;
			left: -10px;
			width: 0;
			color: #fff;
			overflow: hidden;
			background: #1e1e1e;
			-webkit-transition: width 0.22s cubic-bezier(.72,.02,.08,.91);
			-o-transition: width 0.22s cubic-bezier(.72,.02,.08,.91);
			transition: width 0.22s cubic-bezier(.72,.02,.08,.91);
			white-space:nowrap;
		}
	.ProjectEntry .project-container .highlight h1 {
				margin: 5px 10px 5px 10px;
			}
	.ProjectEntry .project-container:hover .highlight {
				width: calc(100% + 20px);
}
	.ProjectEntry .project-image {
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 200%;
		opacity: 0;
		pointer-events: none;
		-webkit-transition: opacity 0.2s cubic-bezier(.7,.04,.08,.91);
		-o-transition: opacity 0.2s cubic-bezier(.7,.04,.08,.91);
		transition: opacity 0.2s cubic-bezier(.7,.04,.08,.91);
	}
.ProjectEntry:hover .project-image {
			opacity: 1;
}


